@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Anton&display=swap');
*{
    user-select: none;
    font-family: "Montserrat";
    /* font-family: "Roboto"; */
}

.main-outer-container, .main-outer-container.scrolling{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    overflow-y: overlay;
}
.main-outer-container::-webkit-scrollbar, .main-outer-container.scrolling::-webkit-scrollbar{
    width: 10px;
}
.main-outer-container::-webkit-scrollbar-thumb{
    width: 10px;
    background-color: gray;
    transition: background-color 0.5s ease-in-out;
}
.main-outer-container::-webkit-scrollbar-track{
    width: 10px;
    border-left: 1px solid rgba(128,128,128,0.2);
    background-color: #202020;
}
.main-outer-container.scrolling::-webkit-scrollbar-thumb{
    width: 10px;
    background-color: gray;
}

