    align-items: center;
    color: white;
    font-size: 1.8em;
    font-weight: 700;
    /* margin-top: 20px; */
    /* padding-left: 30px; */
}
.librarycontainer{
    width: 100%;
    /* background-color: blue; */
    padding: 0px 0px 30px 0px;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    /* margin-top: 20px; */
}

.createrow{
    width: 100%;
    /* height: 150px; */
    /* background-color: white; */
    margin: 5px 0px 5px 0px;
    display: flex;
    /* justify-content: space-between; */
    justify-content: space-between;
    align-items: flex-start;
}
.eachinrow{
    /* width: calc(100% / 4); */
