.createanotherrow{
    width: 100%;
    padding: 0px 0px;
}


.logopart{
    width: 200px;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    /* background-color: aqua; */
}
.logopartdiv{
    width: 65%;
    height: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 20px;
    cursor: pointer;
    /* background-color: blue; */
}
.logopartdiv img{
    width: 25px;
    height: auto;
    cursor: pointer;
}
.logopartdiv p{
    font-size: 1.3em;
    color: white;
    font-family: "anton";
    margin-left: 3px;
    /* padding-top: 2px; */
    cursor: pointer;
    /* letter-spacing: -1px; */
    /* font-weight: 700; */
    font-weight: 400;
    /* padding-right: 5px; */
    cursor: pointer;
    /* background-color: aqua; */
}
.middlepart{
    width: 60%;
    height: 100%;
    /* background-color: aqua; */
    display: flex;
    justify-content: center;
    align-items: center;
}
.centermiddlepart{
    height: 100%;
    width: 45%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    /* background-color: blue; */
}
.tabs,.lighttabs{
    color: white;
    height: 100%;
    letter-spacing: -1px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.1em;
    font-weight: 600;
    padding: 5px 10px;
    cursor: pointer;
    /* background-color: black; */
}
.lighttabs{
    color: rgba(255,255,255,0.5);
}
.searchicon{
    width: 15px;
    height: auto;
    margin-right: 5px;
}
