.profilepart{
    width: 150px;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    /* background-color: aliceblue; */
    padding-right: 25px;
}

.picture-container{
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    border-radius: 50%;
    cursor: pointer;
}

.eachshadow{
    width: 100%;
    height: 100%;
    position: absolute;
    opacity: 0;
    transition: opacity 0.2s ease-in-out;
    background: linear-gradient(to bottom, rgba(0,0,0,0.6), rgba(0,0,0,0), rgba(0,0,0,0))
}
.eachartcover{
    position: relative;
    height: auto;
    width: 100%;
    cursor: pointer;
}
.eachartcover:hover .eachshadow{
    opacity: 1;
    /* transition: opacity 0.2s 0.2s ease-in-out; */
}
.eachalbumart{
    width: 100%;
    height: auto;
    border-radius: 4px;
}
.showplaybutton,.showplaybuttonfixed{
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    padding: 0px 20px 20px 0px;
}
.showplaybuttonfixed{
    opacity: 1;
}
.showplaybutton{
    opacity: 0;
    transition: opacity 0.2s ease-in-out;
}
.eachartcover:hover .showplaybutton{
    opacity: 1;
    /* transition: opacity 0.2s 0.2s ease-in-out; */
}
.innerplaybutton{
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0,0,0,0.7);
    border-radius: 50%;
    transition: background-color 0.2s ease-in-out;
}
.innerplaybutton:hover{
    background-color: rgba(0,0,0,1);
}
.innerplaybutton img{
    width: 40%;
    height: auto;
}


.library-opener{
    position: absolute;
    width: 25px;
    height: 20px;
    top: 10px;
    right: 20px;
    /* background-color: blue; */
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    opacity: 0;
    transition: opacity 0.2s ease-in-out;
}
.eachartcover:hover .library-opener{
    opacity: 1;
}
.library-opener1,.library-opener2,.library-opener3{
    width: 5px;
    height: 5px;
    background-color: rgba(255,255,255,0.7);
    border-radius: 50%;
}
.library-opener:hover .library-opener1,
.library-opener:hover .library-opener2,
.library-opener:hover .library-opener3{
    background-color: rgba(255,255,255,1);
}


.librarygrid{
    width: 100%;
    height: auto;
    display: grid;
    grid-template-columns: repeat(7,12.7%);
    grid-column-gap: calc((100% - 88.9%) / 6);
    /* background-color: aqua; */
}
@media screen and (min-width: 1600px) {
    .eachinrow{
        min-height: 320px;
    }
    .librarygrid{
        grid-template-columns: repeat(8,11%);
        grid-column-gap: calc((100% - 88%) / 7); 
    }
}